* {
  box-sizing: border-box;
}

body {
  background-color: #413c58;
  font-family: Arial, Helvetica, sans-serif;
  margin: 0;
}
.boardgames-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.boardgame {
  background-color: #aeadf0;
  color: #413c58;
  width: 360px;
  margin: 1rem;
  border-radius: 5px;
  overflow: hidden;
  box-shadow: 8px 8px 6px rgba(0, 0, 0, 0.3);
  position: relative;
}

.boardgame img {
  height: 360px;
  width: 360px;
}

.boardgame-info {
  display: flex;
  padding: 1rem;
  align-items: center;
  justify-content: space-between;
}

.boardgame-info span {
  font-size: 1rem;
}

.boardgame-info h3 {
  margin: 0;
  font-size: 1rem;
}

.boardgame-desc {
  background-color: #d8dcff;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 1rem;
  transform: translateY(100%);
  transition: transform 0.4s ease-in-out;
  overflow: auto;
  max-height: 100%;
}

.boardgame:hover .boardgame-desc {
  transform: translateY(0);
}

.boardgame-desc h2 {
  font-size: 1.2rem;
  margin: 0;
  margin-bottom: 0.3rem;
}

.boardgame-desc p {
  font-size: 0.85rem;
  margin: 0;
}
